interface Data {
  SN: number;
  name: string;
  Course: number;
  NumberOfVisits: number;
}

function createData(
  SN: number,
  name: string,
  Course: number,
  NumberOfVisits: number
): Data {
  return {
    SN,
    name,
    Course,
    NumberOfVisits,
  };
}

const rows = [
  createData(1, "Cupcake", 305, 3.7),
  createData(2, "Donut", 452, 25.0),
  createData(3, "Eclair", 262, 16.0),
  createData(4, "Frozen yoghurt", 159, 6.0),
  createData(5, "Gingerbread", 356, 16.0),
  createData(6, "yellow", 356, 16.0),
  createData(7, "Frozan", 356, 16.0),
  createData(8, "Blue", 356, 16.0),
];

function descendingComparator<T>(a: T, b: T, orderBy: keyof T) {
  if (b[orderBy] < a[orderBy]) {
    return -1;
  }
  if (b[orderBy] > a[orderBy]) {
    return 1;
  }
  return 0;
}

type Order = "asc" | "desc";

function getComparator<Key extends keyof any>(
  order: Order,
  orderBy: Key
): (
  a: { [key in Key]: number | string },
  b: { [key in Key]: number | string }
) => number {
  return order === "desc"
    ? (a, b) => descendingComparator(a, b, orderBy)
    : (a, b) => -descendingComparator(a, b, orderBy);
}

// This method is created for cross-browser compatibility, if you don't
// need to support IE11, you can use Array.prototype.sort() directly
function stableSort<T>(
  array: readonly T[],
  comparator: (a: T, b: T) => number
) {
  const stabilizedThis = array.map((el, index) => [el, index] as [T, number]);
  stabilizedThis.sort((a, b) => {
    const order = comparator(a[0], b[0]);
    if (order !== 0) {
      return order;
    }
    return a[1] - b[1];
  });
  return stabilizedThis.map((el) => el[0]);
}

interface HeadCell {
  disablePadding: boolean;
  id: keyof Data;
  label: string;
  numeric: boolean;
}

const headCells: readonly HeadCell[] = [
  {
    id: "SN",
    numeric: false,
    disablePadding: false,
    label: "S/N",
  },
  {
    id: "name",
    numeric: false,
    disablePadding: true,
    label: "Course Name",
  },
  {
    id: "Course",
    numeric: true,
    disablePadding: false,
    label: "Course",
  },
  {
    id: "NumberOfVisits",
    numeric: true,
    disablePadding: false,
    label: "Number of Visits",
  },
];

interface EnhancedTableProps {
  numSelected: number;
  onRequestSort: (
    event: React.MouseEvent<unknown>,
    property: keyof Data
  ) => void;
  onSelectAllClick: (event: React.ChangeEvent<HTMLInputElement>) => void;
  order: Order;
  orderBy: string;
  rowCount: number;
}

function EnhancedTableHead(props: EnhancedTableProps) {
  const { order, orderBy, onRequestSort } = props;
  const createSortHandler =
    (property: keyof Data) => (event: React.MouseEvent<unknown>) => {
      onRequestSort(event, property);
    };

  return (
    <TableHead>
      <TableRow>
        {headCells.map((headCell) => (
          <TableCell
            key={headCell.id}
            sx={{ color: "#225529" }}
            align={headCell.numeric ? "right" : "left"}
            padding={headCell.disablePadding ? "none" : "normal"}
            sortDirection={orderBy === headCell.id ? order : false}
          >
            <TableSortLabel
              active={orderBy === headCell.id}
              direction={orderBy === headCell.id ? order : "asc"}
              onClick={createSortHandler(headCell.id)}
            >
              {headCell.label}
              {orderBy === headCell.id ? (
                <Box component="span" sx={visuallyHidden}>
                  {order === "desc" ? "sorted descending" : "sorted ascending"}
                </Box>
              ) : null}
            </TableSortLabel>
          </TableCell>
        ))}
      </TableRow>
    </TableHead>
  );
}

interface EnhancedTableToolbarProps {
  numSelected: number;
}

function EnhancedTableToolbar(props: EnhancedTableToolbarProps) {
  const { numSelected } = props;

  return (
    <Toolbar
      sx={{
        pl: { sm: 2 },
        pr: { xs: 1, sm: 1 },
        borderBottom: 1,
        borderColor: "#E5E9EB",
        ...(numSelected > 0 && {
          bgcolor: (theme) =>
            alpha(
              theme.palette.primary.main,
              theme.palette.action.activatedOpacity
            ),
        }),
      }}
    >
      {numSelected > 0 ? (
        <Typography
          sx={{ flex: "1 1 100%" }}
          color="inherit"
          variant="subtitle1"
          component="div"
        >
          {numSelected} selected
        </Typography>
      ) : (
        <Typography
          sx={{ flex: "1 1 100%", color: "primary.main", fontWeight: "bold" }}
          variant="h6"
          id="tableTitle"
          component="div"
        >
          SEASON
        </Typography>
      )}
      {numSelected > 0 ? (
        <Tooltip title="Delete">
          <IconButton>
            <DeleteIcon />
          </IconButton>
        </Tooltip>
      ) : (
        ""
      )}
    </Toolbar>
  );
}

const SpecificAnalytics = () => {
  const classes = useStyles();
  const [order, setOrder] = React.useState<Order>("asc");
  const [orderBy, setOrderBy] = React.useState<keyof Data>("SN");
  const [selected, setSelected] = React.useState<readonly string[]>([]);
  const [page, setPage] = React.useState(0);
  const rowsPerPage = 6;

  const handleRequestSort = (
    event: React.MouseEvent<unknown>,
    property: keyof Data
  ) => {
    const isAsc = orderBy === property && order === "asc";
    setOrder(isAsc ? "desc" : "asc");
    setOrderBy(property);
  };

  const handleSelectAllClick = (event: React.ChangeEvent<HTMLInputElement>) => {
    if (event.target.checked) {
      const newSelected = rows.map((n) => n.name);
      setSelected(newSelected);
      return;
    }
    setSelected([]);
  };

  const handleChangePage = (event: unknown, newPage: number) => {
    setPage(newPage - 1);
  };

  const isSelected = (name: string) => selected.indexOf(name) !== -1;

  // Avoid a layout jump when reaching the last page with empty rows.
  const emptyRows =
    page > 0 ? Math.max(0, (1 + page) * rowsPerPage - rows.length) : 0;

  let CurrentLocations = [
    {
      location: "Analytics",
      Url: "/admin/dashboard/analytics",
    },
    {
      location: "Specific Analytics",
      Url: "/admin/dashboard/analytics/specificanalytics",
      highlight: true
      
    },
  ];

  return (
    <>
      <Box className={classes.mainBox}>
        <Box className={classes.headerBar}>
          <Box>
            <SearchBar />
          </Box>

          <Box className={classes.nortificationIconBox}>
            <NotificationsIcon className={classes.nortificationIcon} />
          </Box>
        </Box>

        <Box className={classes.tableBox}>
          <Box>
            <BasicBreadcrumbs CurrentLocations={CurrentLocations} />
          </Box>

          <Box className={classes.specificanalyticsMainbox}>
            <Box className={classes.detailsBox}>
              <Paper
                sx={{ p: 2, minWidth: {md:330}, maxHeight: 650, minHeight: 515 }}
              >
                <Box>
                  <Typography className={classes.sideHeading} variant="h5">
                    Scottsdale Mens A League
                  </Typography>
                </Box>

                <Box sx={{ py: 2 }}>
                  <Typography variant="body2">League Name</Typography>
                  <Box sx={{ py: 1 }}>
                    <TextField
                      placeholder="Scottsdale Mens A League"
                      color="secondary"
                      className={classes.detailsInput}
                      id="outlined-basic"
                      size="medium"
                      variant="outlined"
                    />
                  </Box>
                </Box>

                <Box sx={{ py: 1 }}>
                  <Typography className={classes.detailsText} variant="body2">
                    Total Number of Rounds
                  </Typography>
                  <Typography className={classes.detailsNums} variant="h6">
                    6
                  </Typography>
                </Box>
              </Paper>
            </Box>

            <Box className={classes.sidetableBox}>
              <Paper>
                <EnhancedTableToolbar numSelected={selected.length} />

                <TableContainer>
                  <Table
                    sx={{ minWidth: 750 }}
                    aria-labelledby="tableTitle"
                    size={"medium"}
                  >
                    <EnhancedTableHead
                      numSelected={selected.length}
                      order={order}
                      orderBy={orderBy}
                      onSelectAllClick={handleSelectAllClick}
                      onRequestSort={handleRequestSort}
                      rowCount={rows.length}
                    />
                    <TableBody>
                      {/* if you don't need to support IE11, you can replace the `stableSort` call with:
              rows.sort(getComparator(order, orderBy)).slice() */}
                      {stableSort(rows, getComparator(order, orderBy))
                        .slice(
                          page * rowsPerPage,
                          page * rowsPerPage + rowsPerPage
                        )
                        .map((row, index) => {
                          const isItemSelected = isSelected(row.name);
                          const labelId = `enhanced-table-checkbox-${index}`;

                          return (
                            <TableRow
                              hover
                              role="checkbox"
                              aria-checked={isItemSelected}
                              tabIndex={-1}
                              key={row.name}
                              selected={isItemSelected}
                            >
                              <TableCell sx={{ color: "#225529" }}>
                                {row.SN}
                              </TableCell>
                              <TableCell
                                component="th"
                                id={labelId}
                                scope="row"
                                padding="none"
                              >
                                {row.name}
                              </TableCell>
                              <TableCell align="right">
                                {row.NumberOfVisits}
                              </TableCell>
                              <TableCell align="right">
                                {row.NumberOfVisits}
                              </TableCell>
                            </TableRow>
                          );
                        })}
                      {emptyRows > 0 && (
                        <TableRow
                          style={{
                            height: 53 * emptyRows,
                          }}
                        >
                          <TableCell colSpan={6} />
                        </TableRow>
                      )}
                    </TableBody>
                  </Table>
                </TableContainer>

                <Box className={classes.pagginationBox}>
                  <Pagination
                    color="secondary"
                    count={Math.ceil(rows.length / rowsPerPage)}
                    onChange={handleChangePage}
                    variant="text"
                    shape="rounded"
                  />
                </Box>
              </Paper>
            </Box>
          </Box>
        </Box>
      </Box>
    </>
  );
};

