import * as React from "react";
import Box from "@mui/material/Box";
import Typography from "@mui/material/Typography";
import Modal from "@mui/material/Modal";
import { makeStyles } from "@mui/styles";
import Radio from "@mui/material/Radio";
import RadioGroup from "@mui/material/RadioGroup";
import FormControlLabel from "@mui/material/FormControlLabel";
import FormControl from "@mui/material/FormControl";
import { MenuItem, TextField, Button } from "@mui/material";

const style = {
  position: "absolute" as "absolute",
  top: "50%",
  left: "50%",
  transform: "translate(-50%, -50%)",
  width: 400,
  bgcolor: "background.paper",
  // border: "2px solid #000",
  boxShadow: 24,
  borderRadius: "5px",
  outline: "none",
  p: 3,
};

const Months = [
  {
    value: "6 Month",
    label: "6 Month",
  },
  {
    value: "3 Month",
    label: "3 Month",
  },
  {
    value: "Permanently",
    label: "Permanently",
  },
];

export default function BanModal() {
  const classes = useStyles();
  const [open, setOpen] = React.useState(false);
  const handleClose = () => setOpen(false);
  const [Month, setMonth] = React.useState("6 Month");

  const changeropdownmenu = (event: React.ChangeEvent<HTMLInputElement>) => {
    setMonth(event.target.value);
  };

  return (
    <div>
      <Modal
        open={open}
        onClose={handleClose}
        aria-labelledby="modal-modal-title"
        aria-describedby="modal-modal-description"
      >
        <Box sx={style}>
          <Box>
            <Typography variant="subtitle1" className={classes.heading}>
              BAN USER
            </Typography>
          </Box>

          <Box sx={{ pt: 2 }}>
            <Typography variant="body2" className={classes.subHeading}>
              Ban user from
            </Typography>

            <FormControl sx={{ pt: 2 }}>
              <RadioGroup
                aria-labelledby="demo-radio-buttons-group-label"
                defaultValue="female"
                name="radio-buttons-group"
              >
                <FormControlLabel
                  value="male"
                  control={<Radio size="small" color="secondary" />}
                  label="Male"
                />
                <FormControlLabel
                  value="other"
                  control={<Radio size="small" color="secondary" />}
                  label="Other"
                />
              </RadioGroup>
            </FormControl>
          </Box>
          <Box sx={{ pt: 2 }}>
            <Typography variant="body2" className={classes.subHeading}>
              Duration
            </Typography>

            <Box sx={{ pt: 2 }}>
              <TextField
                className={classes.detailsInput}
                color="secondary"
                id="outlined-select-currency"
                select
                size="small"
                value={Month}
                onChange={changeropdownmenu}
                sx={{ maxWidth: "100%" }}
              >
                {Months.map((option) => (
                  <MenuItem key={option.value} value={option.value}>
                    {option.label}
                  </MenuItem>
                ))}
              </TextField>
            </Box>

            <Box
              sx={{ pt: 2, display: "flex", justifyContent: "space-between" }}
            >
              <Box sx={{ flexBasis: "48%" }}>
                <Button
                  sx={{
                    mb: 2,
                    py: 2,
                    fontWeight: "600",
                    borderRadius: 2,
                    width: "100%",
                  }}
                  color="error"
                  variant="contained"
                  size="small"
                  // onClick={() =>
                  //   navigate(
                  //     '/admin/dashboard/pushnortification/createnortifications/'
                  //   )
                  // }
                >
                  Cancel
                </Button>
              </Box>
              <Box sx={{ flexBasis: "48%" }}>
                <Button
                  sx={{
                    mb: 2,
                    py: 2,
                    fontWeight: "600",
                    borderRadius: 2,
                    width: "100%",
                  }}
                  color="secondary"
                  variant="contained"
                  size="small"
                  // onClick={() =>
                  //   navigate(
                  //     '/admin/dashboard/pushnortification/createnortifications/'
                  //   )
                  // }
                >
                  Ban
                </Button>
              </Box>
            </Box>
          </Box>
        </Box>
      </Modal>
    </div>
  );
}

const useStyles = makeStyles((theme: any) => {
  return {
    heading: {
      color: theme.palette.primary.main,
      fontWeight: "bold !important",
    },
    subHeading: {
      fontWeight: "bold !important",
    },
    detailsInput: {
      width: "100%",

      "& .MuiOutlinedInput-root": {
        "& fieldset": {
          borderColor: "#7D9E49",
          borderRadius: "10px !important",
        },
      },
    },
  };
});
