import * as React from "react";
import Modal from "@mui/material/Modal";
import Typography from "@mui/material/Typography";
import Box from "@mui/material/Box";

export default function ErrorFallback({ message, crash = false }: any) {
  const rootRef = React.useRef<HTMLDivElement>(null);

  return (
    <Box
      sx={{
        height: "100vh",
        flexGrow: 1,
        minWidth: 150,
        transform: "translateZ(0)",
        // The position fixed scoping doesn't work in IE11.
        // Disable this demo to preserve the others.
        "@media all and (-ms-high-contrast: none)": {
          display: "none",
        },
      }}
      ref={rootRef}
    >
      <Modal
        disablePortal
        disableEnforceFocus
        disableAutoFocus
        open
        aria-labelledby="server-modal-title"
        aria-describedby="server-modal-description"
        sx={{
          display: "flex",
          p: 1,
          alignItems: "center",
          justifyContent: "center",
          background: "white",
        }}
        container={() => rootRef.current}
      >
        <Box
          sx={{
            position: "relative",
            width: 400,
            bgcolor: "#225529",
            borderRadius: "8px",
            p: 4,
            color: "white",
          }}
        >
          <Typography
            style={{
              fontWeight: "bold",
              width: "100%",
              textAlign: "center",
              fontSize: "30px",
              marginBottom: "10px",
            }}
            id="server-modal-title"
            variant="h6"
            component="h1"
          >
            Linx League
          </Typography>
          <Typography id="server-modal-title" variant="h6" component="h2">
            Oh no!
          </Typography>
          <Typography id="server-modal-description" sx={{ pt: 2, pb: 2 }}>
            There was an error
          </Typography>
          <Typography
            id="server-modal-description"
            sx={{
              pt: 2,
              width: "100%",
              height: "200px",
              wordWrap: "break-word",
              overflowY: "scroll",
            }}
          >
            {`Error: ${message}`}
          </Typography>
          {crash ? (
            <button
              style={{
                padding: "10px",
                border: "1px solid white",
                background: "transparent",
                color: "white",
                marginTop: "20px",
                cursor: "pointer",
              }}
              onClick={() => window.location.reload()}
            >
              Try Again
            </button>
          ) : (
            <button
              style={{
                padding: "10px",
                border: "1px solid white",
                background: "transparent",
                color: "white",
                marginTop: "20px",
                cursor: "pointer",
              }}
              onClick={() => window.location.reload()}
            >
              Try Again
            </button>
          )}
        </Box>
      </Modal>
    </Box>
  );
}
