interface Data {
  SN: number;
  name: string;
  Handicap: number;
  RoundsPlayed: number;
  LinxScore: number;
  ReferralTracker: number;
}

function createData(
  SN: number,
  name: string,
  Handicap: number,
  RoundsPlayed: number,
  LinxScore: number,
  ReferralTracker: number
): Data {
  return {
    SN,
    name,
    Handicap,
    RoundsPlayed,
    LinxScore,
    ReferralTracker,
  };
}

