interface Data {
  name: string;
  Message: number;
  ActivationTime: number;
  Reciept: number;
  Action: number;
}

function createData(
  name: string,
  Message: number,
  ActivationTime: number,
  Reciept: number,
  Action: any
): Data {
  return {
    name,
    Message,
    ActivationTime,
    Reciept,
    Action,
  };
}

