import * as React from "react";
import Breadcrumbs from "@mui/material/Breadcrumbs";
import { Box } from "@mui/material";
import { makeStyles } from "@mui/styles";
import { Link } from "react-router-dom";

function handleClick(event: React.MouseEvent<HTMLDivElement, MouseEvent>) {
  event.preventDefault();
  console.info("You clicked a breadcrumb.");
}

export default function BasicBreadcrumbs({ CurrentLocations }: any) {
  const classes = useStyles();
  return (
    <Box sx={{ my: 2 }} role="presentation" onClick={handleClick}>
      <Breadcrumbs aria-label="breadcrumb">
        {CurrentLocations?.map((value: any, index: any) => {
          return (
            <Link
              key={index}
              className={
                value.highlight ? classes.PrimaryColor : classes.ColorInherit
              }
              style={{
                textDecoration: "none",
              }}
              to={value.Url}
            >
              {value.location}
            </Link>
          );
        })}
      </Breadcrumbs>
    </Box>
  );
}

const useStyles = makeStyles((theme: any) => {
  return {
    PrimaryColor: {
      color: "#7D9E49",
    },
    ColorInherit: {
      color: "inherit",
    },
  };
});
