import {
  CssBaseline,
  Box,
  Container,
  Typography,
  TextField,
  Grid,
} from "@mui/material";
import { LoadingButton } from "@mui/lab";
import { Logo } from "../../assets";
import { makeStyles } from "@mui/styles";
import { useState } from "react";

function Copyright(props: any) {
  return (
    <Typography
      variant="body2"
      color="text.secondary"
      align="center"
      {...props}
    >
      {"© "}
      {new Date().getFullYear()}
      {" Linx League"}
    </Typography>
  );
}

const Login = () => {
  interface valuesInterface {
    email: any;
    password: any;
  }

  const [values, setValues] = useState({
    email: "",
    password: "",
  } as valuesInterface);

  const classes = useStyles();
  return (
    <>
      <Container
        className={classes.Main}
        sx={{
          minHeight: "100vh",
          display: "flex",
          flexDirection: "column",
          justifyContent: "center",
          alignItems: "center",
        }}
        component="main"
        maxWidth="xs"
      >
        <CssBaseline />
        <Box className={classes.mainBox}>
          <img src={Logo} alt="loading" className={classes.Logo} />

          <Typography sx={{ my: "1rem" }} component="h1" variant="h5">
            Welcome Back !
          </Typography>
          <Typography component="h1" variant="body1">
            Sign in to continue to Linx League.
          </Typography>
          <Box
            component="form"
            // onSubmit={handleSubmit}
            noValidate
            sx={{ mt: 1 }}
          >
            <TextField
              margin="normal"
              required
              fullWidth
              id="email"
              label="Email Address"
              name="email"
              autoComplete="email"
              autoFocus
              onChange={(e) => setValues({ ...values, email: e.target.value })}
            />
            <TextField
              margin="normal"
              required
              fullWidth
              name="password"
              label="Password"
              type="password"
              id="password"
              autoComplete="current-password"
              onChange={(e) =>
                setValues({ ...values, password: e.target.value })
              }
            />

            <LoadingButton
              type="submit"
              fullWidth
              variant="contained"
              className={classes.btnLogin}
              //   loading={isLoading}
              //   disabled={isLoading}
            >
              Log In
            </LoadingButton>
            <Grid container>
              <Grid
                item
                xs
                style={{ display: "flex", justifyContent: "center" }}
              ></Grid>
            </Grid>
          </Box>
        </Box>
        <Copyright sx={{ mt: 8, mb: 4 }} />

        {/* <SnackBar
          anchorOrigin={{ vertical: "top", horizontal: "center" }}
          open={!!snackbar}
          handleClose={() => {
            showSnackbar(null);
          }}
          text={snackbar?.text}
          type={snackbar?.type}
        /> */}
      </Container>
    </>
  );
};

export default Login;

const useStyles = makeStyles((theme: any) => {
  return {
    mainBox: {
      marginTop: 8,
      display: "flex",
      flexDirection: "column",
      alignItems: "center",
    },
    btnLogin: { marginTop: 3, marginBottom: 2 },
    Logo: {
      width: "70%",
      height: "70%",
      filter: "invert(1)",
    },
    Main: {},
  };
});
